// see `./src/styles/layout/container.scss`
// for margin modifications on this module's parent container

.bio-list__list {
  @include reset-list;
  margin: unit(2) 0;
}

.bio-list__item {
  @include clearfix;
  font-size: 1.2rem;
  margin: 0;
  padding: unit(1) 0;

  @include respond-to('medium') {
    font-size: 1.4rem;
    padding: unit(2) 0;
  }

  &:first-child {
    padding-top: 0;
  }

  &:last-child {
    padding-bottom: 0;
  }

  + .bio-list__item {
    border-top: 1px solid $border--light;
  }

  [class*='heading-'] {
    margin-bottom: unit(0.5);
  }

  p {
    margin: unit(0.5) 0;
  }
}

.bio-list__image {
  float: left;
  margin: 0 unit(1) unit(1) 0;
  width: unit(5);

  @include respond-to('small') {
    margin-bottom: 0;
    width: 22%;
  }
}

.bio-list__content {
  @include null-margins;

  .-has-image & {
    float: right;
    width: calc(100% - #{unit(6)});

    @include respond-to('small') {
      width: calc(78% - #{unit(1)});
    }
  }
}

.bio-list__link {
  font-size: 1.4rem;
  line-height: 1.3;
  margin-top: unit(1);

  &.-large {
    display: none;

    @include respond-to('small') {
      display: block;
    }
  }

  &.-small {
    clear: both;
    text-align: center;

    @include respond-to('small') {
      display: none;
    }
  }

  a {
    @include arrow-link('orange');

    .-light-gray & {
      @include arrow-link('dark-orange');
      color: darken($color--rose-of-sharon, 3);
    }
  }
}
